
.stochastic path {
  fill: none;
  stroke-width: 1;
}
.stochastic.up {
  stroke: #00AA00;
}
.stochastic.down {
  stroke: #FF0000;
}
.stochastic path.overbought,
.stochastic path.oversold {
  stroke: #FF9999;
  stroke-dasharray: 5, 5;
}



